/// <reference path="../definition/react.d.ts" />
/// <reference path="../definition/react-dom.d.ts" />
import React = require('react');
import ReactDOM = require('react-dom');

import Widget = require("../widget/widget");

export interface LikeButtonProps extends Widget.WidgetProps {
	liked?: boolean
}
export interface LikeButtonStates {
	liked?: boolean,
	clickCount?: number
}

export class LikeButton extends Widget.Widget<LikeButtonProps, LikeButtonStates> {
	static defaultProps: LikeButtonProps = Widget.mergeProps({
		liked: false
	}, Widget.Widget.defaultProps)
	state: LikeButtonStates;

	constructor(props: LikeButtonProps) {
		super(props);
		this.state = { liked: this.props.liked, clickCount: 0 };
	}
	handleClick(event: any): void {
		this.setState({ liked: !this.state.liked, clickCount: this.state.clickCount + 1 });
	}

	getWidgetSclass(): string {
		return 'likebutton'
	}
	getRenderChildren(): React.ReactNode {
		// console.log(this.props);
		var text = this.state.liked ? 'like' : 'haven\'t liked';
		return (
			<div>
				<span onClick={e => this.handleClick(e) } style={{ color: 'blue' }}>
					Click ({this.state.clickCount}) to toggle.
				</span>
				You {text} this.
			</div>
		);
    }
}